window.addEventListener("DOMContentLoaded", function () {
	const header = document.querySelector(".header");
	const portfolioItems = document.querySelectorAll(".portfolio__item");

	const toggleMenu = () => {
		const nav = document.querySelector(".navigation");
		nav.classList.toggle("is-active");
		if (nav.classList.contains("is-active")) {
			document.body.classList.add("scroll-stopped");
		} else {
			document.body.classList.remove("scroll-stopped");
		}
	};

	function clickHandler(e) {
		const target = e.target;
		const menuOpen = target.closest(".header__burger");
		const menuClose = target.closest(".navigation__close");

		if (menuOpen || menuClose) {
			toggleMenu();
		}
	}

	header.addEventListener("click", clickHandler);

	const mouseHandler = (e) => {
		const target = e.target;
		const portfolio = target.closest(".portfolio");
		const portfolioItem = target.closest(".portfolio__item");
		const portfolio__list = target.matches(".portfolio__list");

		if (portfolio__list || !portfolio) {
			portfolioItems.forEach((item) => item.classList.remove("is-active"));
		} else if (portfolioItem) {
			portfolioItems.forEach((item) =>
				item.classList[item === portfolioItem ? "add" : "remove"]("is-active")
			);
		}
	};

	document.addEventListener("mouseover", mouseHandler);
});
$(document).ready(function () {
	const $grid = $(".portfolio__list").masonry({
		itemSelector: ".portfolio__item",
		gutter: ".portfolio__gutter",
	});

	$grid.imagesLoaded().progress(function () {
		$grid.masonry("layout");
	});
});
jQuery(document).ready(function ($) {
	const sr = ScrollReveal({
		distance: "30%",
		duration: 2000,
		delay: 200,
	});

	sr.reveal(".header", { origin: "top", delay: 0, duration: 1000 });
	sr.reveal(".footer", { origin: "bottom", delay: 0, duration: 1000 });
	sr.reveal(".home__content");

	$(".navigation__link").each((idx, item) => {
		$(item).on("click", function () {
			if ($(".navigation").hasClass("is-active")) {
				$(".navigation").removeClass("is-active");
				$("body").removeClass("scroll-stopped");
			}
		});
	});

	const scrollAdaptive = (breakpoint) => {
		if (breakpoint.matches) {
			sr.reveal(".home__content");
			sr.reveal(".about__left", {
				origin: "left",
				delay: 0,
				duration: 2000,
			});
			sr.reveal(".about__title", { delay: 100 });
			sr.reveal(".about__row", { delay: 100 });
			sr.reveal(".about__links", { delay: 100 });
			sr.reveal(".awards__title", { origin: "left" });
			sr.reveal(".awards__item", {
				interval: 200,
				distance: "60px",
				origin: "left",
			});
			sr.reveal(".contacts__info", { origin: "left" });
			sr.reveal(".contacts__form", { origin: "bottom" });

			const PP = $("#pagepiling");
			PP.attr({
				style: "",
			});
			PP.find(".pp-section").css({
				zIndex: "",
				transform: "",
				"-webkit-transform": "",
				"-moz-transform": "",
				"-ms-transform": "",
			});
			PP.find(".pp-section").removeClass(
				"pp-section pp-table pp-scrollable active pp-easing"
			);

			PP.find("li").css({
				zIndex: "",
				transform: "",
				"-webkit-transform": "",
				"-moz-transform": "",
				"-ms-transform": "",
			});

			PP.find(".pp-tableCell").removeClass("pp-tableCell");

			$("body, html").css({
				overflow: "initial",
				"-ms-touch-action": "initial",
				"touch-action": "initial",
			});

			$("#pp-nav").remove();

			$(window).on("scroll", scrollHandler);
		} else {
			$(window).off("scroll", scrollHandler);
			$("body, html").css({
				overflow: "",
				"-ms-touch-action": "",
				"touch-action": "",
			});
			$("#pagepiling").pagepiling({
				anchors: ["home", "about", "portfolio", "awards", "contacts"],

				onLeave: function (index, nextIndex, direction) {
					switch (nextIndex) {
						case 1:
							sr.reveal(".home__content");
							break;

						case 2:
							sr.reveal(".about__left", {
								origin: "left",
								delay: 400,
								duration: 2000,
							});
							sr.reveal(".about__title");
							sr.reveal(".about__row");
							sr.reveal(".about__links", { delay: 1000 });
							break;
						case 3:
							break;
						case 4:
							sr.reveal(".awards__title", { origin: "left" });
							sr.reveal(".awards__item", { interval: 200, distance: "100px" });
							break;
						case 5:
							sr.reveal(".contacts__info", { origin: "left" });
							sr.reveal(".contacts__form");
							break;
					}
				},

				afterLoad: function (anchorLink, index) {
					$(".navigation__link").each((idx, item) => {
						if (item.href.includes(anchorLink)) {
							$(item).addClass("current");
						} else {
							$(item).removeClass("current");
						}
					});

					if (anchorLink !== "home" && anchorLink !== "contacts") {
						$(".header").addClass("header--dark");
						$(".footer").addClass("footer--dark");
						$("#pp-nav").addClass("is-dark");
					} else {
						$(".header").removeClass("header--dark");
						$(".footer").removeClass("footer--dark");
						$("#pp-nav").removeClass("is-dark");
					}
				},
			});
		}
	};

	const checkWidth = () => {
		const breakpoint = window.matchMedia("(max-width: 1024px)");

		scrollAdaptive(breakpoint);

		breakpoint.addEventListener("change", scrollAdaptive);
	};

	checkWidth();

	function scrollHandler() {
		if ($(window).scrollTop() > 100) {
			$(".header").addClass("header--white");
		} else {
			$(".header").removeClass("header--white");
		}

		var $sections = $("section");
		$sections.each(function (i, el) {
			var top = $(el).offset().top - 100;
			var bottom = top + $(el).height();
			var scroll = $(window).scrollTop();
			var id = $(el).attr("id");
			if (scroll > top && scroll < bottom) {
				if ($("a.current").attr("href").includes(id)) {
					return;
				}
				$(".header").remove("header--dark");
				if (id === "about" || id === "awards") {
					$(".header").addClass("header--dark");
					$(".footer").addClass("footer--dark");
				}
				if (id === "home" || id === "contacts") {
					$(".header").removeClass("header--dark");
					$(".footer").removeClass("footer--dark");
				}
				$("a.current").removeClass("current");
				$('a[href="#' + id + '"]').addClass("current");
			}
		});
	}
});
window.addEventListener("DOMContentLoaded", function () {
	function validateEmail(email) {
		const re =
			/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
		return re.test(String(email).toLowerCase());
	}

	function validate(object) {
		let input = null;

		if (object.target) {
			input = object.target;
		} else {
			input = object;
		}
		const parent = input.closest(".input-group");
		const error = parent.querySelector("p");

		if (input.value.length < 1) {
			parent.classList.add("has-error");
			error.textContent = "  ";
		} else {
			parent.classList.remove("has-error");
		}

		if (input.name === "email" && input.value.length > 1) {
			if (validateEmail(input.value)) {
				parent.classList.remove("has-error");
			} else {
				parent.classList.add("has-error");
				error.textContent = "  email";
			}
		}
	}

	async function formSend(form) {
		const modal = document.querySelector(".modal");
		const modalDialog = modal.querySelector(".modal__dialog");
		const modalTitle = modal.querySelector(".modal__title");
		const modalText = modal.querySelector(".modal__text");

		modal.classList.add("is-active");
		modal.classList.add("pending");

		const data = new FormData(form);

		const response = await fetch("send.php", {
			method: "POST",
			body: data,
		});

		if (response.ok) {
			let result = await response.json();
			form.reset();

			modalTitle.textContent = "Thank you!";
			modalText.textContent = "We will contact you soon.";
			modal.classList.remove("pending");
		} else {
			modalTitle.textContent = "Ooops...";
			modalText.textContent = "Something went wrong, please try again later.";
			modal.classList.remove("pending");
		}

		setTimeout(() => {
			modal.classList.remove("is-active");
		}, 3000);
	}

	function submitHandler(e) {
		const form = e.target;
		e.preventDefault();

		if (!form) return;

		const inputs = form.querySelectorAll(".required");

		inputs.forEach((input) => {
			validate(input);
			input.addEventListener("blur", validate);
		});

		const invalidInputs = form.querySelectorAll(".has-error");

		if (invalidInputs.length === 0) {
			formSend(form);
		}
	}

	document.addEventListener("submit", submitHandler);
});